.statistic {
  width: 100%;
  padding: 10px;
  border: 2px solid;
  border-color: darkcyan;
  border-radius: 4px;
  background-color: cornsilk;
  text-align: center;
}

.list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.item {
  width: 70px;
  height: 50px;
  margin: 2px;

  background-color: whitesmoke;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.label {
  font-weight: bold;
}
